import { html } from 'lit';
import { classMap } from 'lit/directives/class-map.js';
import range from 'lodash/range';
import { clickOutside } from '../../utils/directives/clickOutside';
import { isVisible } from '../../utils/directives/isVisible';
import { MONTHS, LEFT_ARROW_TEMPLATE, RIGHT_ARROW_TEMPLATE, SMALL_ARROW_UP_TEMPLATE, SMALL_ARROW_DOWN_TEMPLATE } from './constants';
export const getMonthsDialogTemplate = (currentMonth, clickCallback) => {
    return html `
        <div class="dialog__container dialog__grid container level-2">
            ${MONTHS.map((month, index) => html `
                    <div
                        class="dialog__month flex-centred selectable ${classMap({
        selected: index === currentMonth,
    })}"
                        @click=${() => clickCallback(index)}
                    >
                        ${month}
                    </div>
                `)}
        </div>
    `;
};
export const getYearsDialogTemplate = (currentYear, offsetByYear, arrowClickCallback, selectYearCallback) => {
    return html `
        <div class="dialog__container level-2">
            <div class="year-action">
                <div
                    class="year-action__arrow"
                    @click=${() => arrowClickCallback(-1)}
                >
                    ${LEFT_ARROW_TEMPLATE}
                </div>
                ${currentYear}
                <div
                    class="year-action__arrow"
                    @click=${() => arrowClickCallback(1)}
                >
                    ${RIGHT_ARROW_TEMPLATE}
                </div>
            </div>
            <div class="dialog__grid container">
                ${range(12).map((index) => {
        const calculatedYear = currentYear + offsetByYear + index;
        return html `
                        <div
                            class="dialog__year flex-centred selectable ${classMap({
            selected: calculatedYear === currentYear,
        })}"
                            @click=${() => selectYearCallback(calculatedYear)}
                        >
                            ${calculatedYear}
                        </div>
                    `;
    })}
            </div>
        </div>
    `;
};
export const getDayTemplate = (date, targetDate, currentDate, selectedDate, selectDayCallback) => {
    const day = date.getDate();
    const month = date.getMonth();
    const year = date.getFullYear();
    const targetMonth = targetDate.getMonth();
    const targetYear = targetDate.getFullYear();
    const isTargetMonth = areEqualDates(new Date(year, month), new Date(targetYear, targetMonth));
    return html `
        <div
            @click=${() => selectDayCallback(date)}
            class="day value selectable flex-centred ${classMap({
        'target-month': isTargetMonth,
        'other-month': !isTargetMonth,
        'current-day': areEqualDates(new Date(currentDate.getFullYear(), currentDate.getMonth(), currentDate.getDate()), date),
        selected: areEqualDates(selectedDate, date),
    })}"
        >
            ${day}
        </div>
    `;
};
export const areEqualDates = (date1, date2) => {
    return (date1 !== undefined &&
        date2 !== undefined &&
        date1.toDateString() === date2.toDateString());
};
export const getMonthsContainerTemplate = (isShowingMonthsDialog, targetMonth, clickMonthCallback, setMonthCallback, hideMonthsDialog) => html `
    <div
        class="calendar-container__month-container"
        ${clickOutside(() => hideMonthsDialog())}
    >
        <div class="calendar-container__button" @click=${clickMonthCallback}>
            ${MONTHS[targetMonth]}
            <i>
                ${isShowingMonthsDialog
    ? SMALL_ARROW_UP_TEMPLATE
    : SMALL_ARROW_DOWN_TEMPLATE}
            </i>
        </div>
        ${isVisible(isShowingMonthsDialog, getMonthsDialogTemplate(targetMonth, setMonthCallback))}
    </div>
`;
export const getYearsContainerTemplate = (isShowingYearsDialog, targetYear, offsetByYear, addOffsetCallback, clickYearCallback, setYearCallback, hideYearsDialog) => html `
    <div
        class="calendar-container__year"
        ${clickOutside(() => hideYearsDialog())}
    >
        <div class="calendar-container__button" @click=${clickYearCallback}>
            ${targetYear}
            <i>
                ${isShowingYearsDialog
    ? SMALL_ARROW_UP_TEMPLATE
    : SMALL_ARROW_DOWN_TEMPLATE}
            </i>
        </div>
        ${isVisible(isShowingYearsDialog, getYearsDialogTemplate(targetYear, offsetByYear, addOffsetCallback, setYearCallback))}
    </div>
`;
export const getCalendarCaptionTemplate = (isShowingMonthsDialog, isShowingYearsDialog, targetDate, offsetByYear, subMonthCallback, addMonthCallback, clickMonthCallback, clickYearCallback, addOffsetCallback, setYearCallback, setMonthCallback, hideMonthsDialog, hideYearsDialog) => {
    const targetMonth = targetDate.getMonth();
    const targetYear = targetDate.getFullYear();
    return html `
        <div class="calendar-caption">
            <div class="calendar-container__button" @click=${subMonthCallback}>
                ${LEFT_ARROW_TEMPLATE}
            </div>
            <div class="calendar-container__dialogs-container head-5">
                ${getMonthsContainerTemplate(isShowingMonthsDialog, targetMonth, clickMonthCallback, setMonthCallback, hideMonthsDialog)}
                <span class="separator"></span>
                ${getYearsContainerTemplate(isShowingYearsDialog, targetYear, offsetByYear, addOffsetCallback, clickYearCallback, setYearCallback, hideYearsDialog)}
            </div>
            <div class="calendar-container__button" @click=${addMonthCallback}>
                ${RIGHT_ARROW_TEMPLATE}
            </div>
        </div>
    `;
};
