import { CALENDAR_DATE_PLACEHOLDER } from './constants';
export const formatDate = (date, format) => {
    if (date === undefined) {
        return '';
    }
    let result = format;
    const numberLength = 2;
    const filler = '0';
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.days, String(date.getDate()).padStart(numberLength, filler));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.months, String(date.getMonth() + 1).padStart(numberLength, filler));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.fullYears, String(date.getFullYear()));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.shortYears, String(date.getFullYear() % 100).padStart(numberLength, filler));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.hours, String(date.getHours()).padStart(numberLength, filler));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.minutes, String(date.getMinutes()).padStart(numberLength, filler));
    result = result.replace(CALENDAR_DATE_PLACEHOLDER.seconds, String(date.getSeconds()).padStart(numberLength, filler));
    return result;
};
