import { FieldType } from '../../table/table.type';
export const isInput = (field) => {
    return field.type === FieldType.number || field.type === FieldType.text;
};
export const isCalendar = (field) => {
    return field.type === FieldType.date || field.type === FieldType.dateRange;
};
export const isDropdown = (field) => {
    return field.type === FieldType.list || field.type === FieldType.dictionary;
};
export const isBoolean = (field) => {
    return field.type === FieldType.boolean;
};
