export const TAG_NAME = 'tsc-input-calendar';
export const CALENDAR_DATE_PLACEHOLDER = {
    hours: 'hh',
    minutes: 'mm',
    seconds: 'ss',
    days: 'DD',
    months: 'MM',
    fullYears: 'YYYY',
    shortYears: 'YY'
};
export const DEFAULT_FORMAT = `${CALENDAR_DATE_PLACEHOLDER.days}.${CALENDAR_DATE_PLACEHOLDER.months}.${CALENDAR_DATE_PLACEHOLDER.fullYears}`;
export const PLASEHOLDERS = Object.values(CALENDAR_DATE_PLACEHOLDER).join('');
