import { SpinnerSize } from './types';
export function getSpinnerSize(size) {
    switch (size) {
        case SpinnerSize.XS:
            return 'calc(var(--base-size) / 2 * 15)';
        case SpinnerSize.S:
            return 'calc(var(--base-size) / 2 * 19)';
        case SpinnerSize.M:
            return 'calc(var(--base-size) / 2 * 23)';
        case SpinnerSize.L:
            return 'calc(var(--base-size) / 2 * 27)';
        default:
            return 'calc(var(--base-size) / 2 * 23)';
    }
}
